import React from 'react';
import Modernizr from 'modernizr';
import './styles/support-detection.scss';

class SupportDetection extends React.Component {
    constructor(props) {
        super(props);
        this.state = { hasError: false };
    }

    componentWillMount() {
        console.log('cssborderimage', Modernizr.borderimage);
        console.log('csstransforms3d', Modernizr.csstransforms3d);
        console.log('wrapflow', Modernizr.wrapflow);

        console.log('es6array', Modernizr.es6array);
        console.log('es6object', Modernizr.es6object);

        console.log('csstransforms', Modernizr.csstransforms);
        console.log('cssflexbox', Modernizr.flexbox);
        console.log('flexboxlegacy', Modernizr.flexboxlegacy);
        console.log('flexboxtweener', Modernizr.flexboxtweener);
        console.log('flexwrap', Modernizr.flexwrap);

        console.log('displaytable', Modernizr.displaytable);
        console.log('subpixelfont', Modernizr.subpixelfont);
        console.log('csscolumns', Modernizr.csscolumns);

        console.log('boxsizing', Modernizr.boxsizing);
        // // console.log('objectfit', Modernizr.objectfit);
        // if (Modernizr.flexbox && Modernizr.flexwrap) {
        //     console.log('supported');
        //     // Modern Flexbox with `flex-wrap` supported
        // } else {
        //     console.log('not supported');
        //     // Either old Flexbox syntax, or `flex-wrap` not supported
        // }

        console.log('history', Modernizr.history);
        console.log('localstorage', Modernizr.localstorage);

        if (
            /**
            * style
            */
            !Modernizr.csstransforms
            || !Modernizr.flexbox
            /**
            * React Mobx
            */
            || !Modernizr.csstransforms
            || !Modernizr.es6array
            /**
            * Tech
            */
            || !Modernizr.localstorage
            || !Modernizr.history
        ) {
            this.setState({ hasError: true });
        }
    }

    componentDidCatch(error, info) {
        this.setState({ hasError: true });
        console.error(error, info);
    }

    render() {
        if (this.state.hasError) {
            return (
                <div className="cq-not-supported">
                    <div className="cq-logo" />
                    <div className="cq-icon-not-supported" />
                    <h1>YOUR BROWSER IS NOT SUPPORTED</h1>
                    <p>Binary.com does not support your browser. Please update your browser.</p>
                    <a href="some url"> Update Browser </a>
                </div>
            );
        }
        return this.props.children;
    }
}


export default SupportDetection;
